<template>
  <el-dialog :lock-scroll="true" :top="'50px'" :width="'60%'" v-model="show" title="Teste">

    <template v-slot:title>
      <div  style="border-bottom: #e0e0e0 1px solid;padding: 20px;">
        <div class="modal-title" style="display: flex;width: calc(100% - 50px)">

          <el-input v-model="query" :placeholder="KT('user.search')" style="--el-input-border-radius: 5px;margin-right: 5px;"></el-input>

          <el-tooltip
              effect="dark"
              :content="KT('user.add')"
              placement="bottom"
          >
            <el-button type="primary" @click="editUserRef.newUser()"><i class="fas fa-user-plus"></i></el-button>
          </el-tooltip>


        </div>
      </div>
    </template>
    <template v-slot:footer>
      <div  style="border-top: #e0e0e0 1px solid;padding: 20px;display: flex;justify-content: flex-start;">

        <el-tooltip
            effect="dark"
            :content="KT('user.remove')"
            placement="bottom"
        >
          <el-button type="danger" :plain="selected===0" @click="doDelete()">
            <i class="fas fa-user-minus"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip
            effect="dark"
            :content="KT('user.edit')"
            placement="bottom"
        >
          <el-button type="warning" :plain="selected===0"  @click="editUserRef.editUser(selected);">
            <i class="fas fa-user-edit"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('user.users')"
            placement="bottom"
        >
          <template :set="user = store.getters['users/getUser'](selected)">
            <el-button  plain :disabled="selected===0 || (user && (user.userLimit===-1 || user.userLimit>0))" @click="linkObjectsRef.showObjects({userId: selected, type: 'users'});">
              <i class="fas fa-users"></i>
            </el-button>
          </template>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('device.devices')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0"  @click="linkObjectsRef.showObjects({userId: selected, type: 'devices'});">
            <i class="fas fa-car"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('geofence.geofences')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0"  @click="linkObjectsRef.showObjects({userId: selected, type: 'geofences'});">
            <i class="fas fa-draw-polygon"></i>
          </el-button>
        </el-tooltip>



        <el-tooltip
            effect="dark"
            :content="KT('group.groups')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'groups'});">
            <i class="far fa-object-group"></i>
          </el-button>
        </el-tooltip>




        <el-tooltip
            effect="dark"
            :content="KT('notification.notifications')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'notifications'});">
            <i class="far fa-envelope"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('calendar.calendars')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'calendars'});">
            <i class="far fa-calendar-alt"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('attribute.computedAttributes')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'attributes'});">
            <i class="fas fa-server"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('driver.drivers')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'drivers'});">
            <i class="fas fa-user-tag"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('command.savedCommands')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'commands'});">
            <i class="far fa-keyboard"></i>
          </el-button>
        </el-tooltip>


        <el-tooltip
            effect="dark"
            :content="KT('maintence.maintence')"
            placement="bottom"
        >
          <el-button  plain :disabled="selected===0" @click="linkObjectsRef.showObjects({userId: selected, type: 'maintence'});">
            <i class="fas fa-tools"></i>
          </el-button>
        </el-tooltip>


      </div>
    </template>
    <div class="itm" style="display: flex;background: #eeeeee;">
      <div style="flex: 1;padding: 10px;font-size: 12px;">{{KT('user.name')}}</div>
      <div style="flex: 1;padding: 10px;font-size: 12px;text-align: center;">{{KT('user.email')}}</div>
      <div style="width: 90px;padding: 10px;font-size: 12px;text-align: center;">{{KT('user.admin')}}</div>
      <div style="width: 92px;padding: 10px;font-size: 12px;text-align: center;">{{KT('user.disabled')}}</div>
    </div>
    <div style="height: calc(100vh - 300px); overflow: hidden; overflow-y: scroll;">



      <div class="itm" v-for="(u,k) in store.state.users.userList" :key="k" @click="selected = (selected!==u.id)?u.id:0" :class="{tr1: (k%2),tr2: !(k%2),selected: (selected === u.id)}" style="display: flex;">

        <div style="flex: 1;padding: 10px;font-size: 14px;">{{u.name}}</div>
        <div style="flex: 1;padding: 10px;font-size: 14px;text-align: center;">{{u.email}}</div>
        <div style="width: 90px;padding: 10px;font-size: 14px;text-align: center;">{{(u.administrator)?'Sim':'No'}}</div>
        <div style="width: 90px;padding: 10px;font-size: 14px;text-align: center;">{{(u.disabled)?'Sim':'No'}}</div>
      </div>

    </div>

  </el-dialog>
</template>


<script setup>
import {ref,defineExpose,inject} from 'vue';
import {useStore} from 'vuex'
import {ElMessage, ElMessageBox, ElNotification} from "element-plus";


const store = useStore();
const query = ref('');
const selected = ref(0);
const show = ref(false);

const editUserRef = inject('edit-user');
const linkObjectsRef = inject('link-objects');


const doDelete = () =>{

  if(selected.value===0){
    ElMessage.error('Voc precisa selecionar um usurio');
    return false;
  }

  if(selected.value===store.state.auth.id){
    ElMessage.error('Voc no pode se deletar!');
    return false;
  }


  const user = store.getters["users/getUser"](selected.value);

  if(user.id < store.state.auth.id){
    ElMessage.error('Voc no pode deletar um admin superior a voc!');
    return false;
  }

  ElMessageBox.confirm(
      'Voc est excluindo o usurio "'+user.name+'", deseja continuar?',
      'Perigo!',
      {
        confirmButtonText: 'Excluir',
        confirmButtonClass: 'danger',
        cancelButtonText: 'Cancelar',
        type: 'warning',
      }
  ).then(()=>{

    store.dispatch("users/deleteUser",selected.value).then(()=> {

      ElNotification({
        title: 'Successo',
        message: 'Usurio deletado com sucesso.',
        type: 'success',
      });
      selected.value = 0;
    }).catch((e)=>{

      ElNotification({
        title: 'Erro',
        message: e.response.data,
        type: 'danger',
      });
    });

  }).catch(()=>{

    ElMessage.error('Ao cancelada pelo usurio');
  })
}


const showUsers = ()=>{
    show.value = true;
}


defineExpose({
  showUsers
});





</script>

<style>

.itm{
  border-bottom: silver 1px dotted;
}

.itm div{
  border-right: silver 1px dotted;
}


.tr1{
  background: #f3f3f3;
}

.tr2{
  background: #f8f8f8;
}

.selected{
  background: rgba(5, 167, 227, 0.05) !important;
}

.itm div:last-child{
  border-right: none;
}

.el-select.el-select--large{
  width: 100%;
}

.el-dialog__header,.el-dialog__body,.el-dialog__footer{
  padding: 0px !important;
}

.el-dialog__footer{
  margin-top: 0px;
}

.el-tabs__nav-wrap{
  padding-left: 20px;
  padding-right: 20px;
}

.el-tabs__content{
  padding-left: 20px;
  padding-right: 20px;
}

.el-form--label-top .el-form-item__label{
  padding: 0px !important;
  margin-bottom: -5px !important;
}
.el-form-item{
  margin-bottom: 5px !Important;
}

.danger{
  --el-button-text-color: var(--el-color-danger) !important;
  --el-button-bg-color: #fef0f0 !important;
  --el-button-border-color: #fbc4c4 !important;
  --el-button-hover-text-color: var(--el-color-white) !important;
  --el-button-hover-bg-color: var(--el-color-danger) !important;
  --el-button-hover-border-color: var(--el-color-danger) !important;
  --el-button-active-text-color: var(--el-color-white) !important;
  --el-button-active-border-color: var(--el-color-danger) !important;
}

</style>