

export default {
    namespaced: true,
    state: () => ({
        serverInfo: {}
    }),
    getters: {
    },
    mutations: {
        setServer(state,value){
            state.serverInfo = value;
        }
    },
    actions: {
        load(context){
            return new Promise((resolve,reject)=> {
                const traccar = window.$traccar;
                traccar.getServer().then(({data}) => {
                    context.commit("setServer", data);
                    resolve(data);
                }).catch(()=>{
                    reject();
                });

            });
        },
        save(context,params) {
            return new Promise((resolve, reject) => {
                window.$traccar.saveServer(params).then(({data}) => {
                    context.commit("setServer", data);
                    resolve(data);
                }).catch(() => {
                    reject()
                });
            })
        },
        addFavAttr(context,params){
            let attr = context.state.serverInfo.attributes['tarkan.deviceAttributes'];
            if(params[1]) {
                if (attr) {
                    attr += ',' + params[0];
                } else {
                    attr = params[0];
                }
            }else{
                if(attr){
                    attr = attr.split(",");
                    attr.splice(attr.findIndex((a)=> a === params[0]),1);

                    attr = attr.join(",");
                }else{
                    attr = '';
                }
            }

            let server = context.state.serverInfo;
                server.attributes['tarkan.deviceAttributes'] = attr;


            context.dispatch("save",server);
            context.commit("setServer",server);
        }
    }
}
