
import {createRouter,createWebHashHistory} from 'vue-router';
import store from './store/index'

const routes = [
    {path: '/:pathMatch(.*)*',redirect: '/home'},
    { path: '/home', component: ()=>import('./templates/home'), meta: {closed: true}},
    {
        path: '/devices',
        meta: {title: 'Dispositivos',shown: true},
        component: ()=>import("./components/empty-rv"),
        children: [
            { path: '', components: {default: ()=>import('./templates/devices')}, meta: {closed: true,allowExpand: false,backBtn: '/home'}},
            { path: ':deviceId', components: {default: ()=>import('./templates/devices.internal')}, meta: {mobileBottom: true,closed: true,backBtn: '/devices'}}
        ]
    },
    { path: '/computed', component: ()=>import('./templates/computed'), meta: {title: 'Atributos Computados',shown: true}},
    { path: '/notifications', component: ()=>import('./templates/notifications'), meta: {title: 'Notificaes',shown: true}},
    { path: '/history', component: ()=>import('./templates/history'), meta: {title: 'Histrico de Rotas',shown: true}},
    { path: '/geofence', component: ()=>import('./templates/geofence'), meta: {title: 'Geocercas',shown: true}},
    {
        path: '/users',
        component: ()=>import('./templates/users'),
        meta: {title: 'Usurios',shown: true}
    },
    { path: '/groups', component: ()=>import('./templates/groups'), meta: {title: 'Grupos',shown: true}},
    { path: '/commands', component: ()=>import('./templates/commands'), meta: {title: 'Comandos',shown: true} },
    {path: '/login', component: ()=>import('@/templates/login.vue'),name: 'Login', meta: {public: true}}
]

const router = createRouter({
    history: createWebHashHistory(),
    routes, // short for `routes: routes`
})

router.beforeEach((to, from, next) => {
    if(store.state.auth || to.meta.public){
        next();
    }else{
        next('/login')
    }
})

export default router;