<template>
  <el-dialog :lock-scroll="true" v-model="show">

    <template v-slot:title>
      <div  style="border-bottom: #e0e0e0 1px solid;padding: 20px;">
        <div class="modal-title" >{{title}}</div>
      </div>
    </template>
    <template v-slot:footer>
      <div  style="border-top: #e0e0e0 1px solid;padding: 20px;display: flex;justify-content: space-between;">

        <el-button type="danger" plain @click="doCancel()">Cancelar</el-button>
        <el-button type="primary" @click="doSave()">Salvar</el-button>
      </div>
    </template>

    <el-tabs v-model="tab">
      <el-tab-pane label="Dados da Conta" name="first">
        <el-form label-width="120px" label-position="top">
          <el-form-item label="Nome" >
            <el-input v-model="formData.name" :disabled="isSupAdmin"></el-input>
          </el-form-item>

          <el-form-item label="Telefone" >
            <el-input v-model="formData.phone" :disabled="isSupAdmin"></el-input>
          </el-form-item>

          <el-form-item label="E-Mail" >
            <el-input v-model="formData.email" :disabled="isSupAdmin"></el-input>
          </el-form-item>
          <el-form-item label="Senha" >
            <el-input v-model="formData.password" :disabled="isSupAdmin" type="password"></el-input>
          </el-form-item>

        </el-form>


      </el-tab-pane>

      <el-tab-pane label="Preferncias" name="second">

        <el-form label-width="120px" label-position="top">
        <el-form-item label="Mapa" >
          <el-input v-model="formData.map" :disabled="isSupAdmin"></el-input>
        </el-form-item>


        <el-form-item label="Latitude" >
          <el-input v-model="formData.latitude" :disabled="isSupAdmin"></el-input>
        </el-form-item>
        <el-form-item label="Longitude" >
          <el-input v-model="formData.longitude" :disabled="isSupAdmin"></el-input>
        </el-form-item>
        <el-form-item label="Zoom" >
          <el-input v-model="formData.zoom" :disabled="isSupAdmin"></el-input>
        </el-form-item>


        <el-form-item label="Formato de 12 Horas" >
          <el-input v-model="formData.zoom" :disabled="isSupAdmin"></el-input>
        </el-form-item>


        <el-form-item label="Formato de Coordenadas" >
          <el-input v-model="formData.zoom" :disabled="isSupAdmin"></el-input>
        </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane v-if="store.state.auth.administrator || store.state.auth.id !== formData.id" label="Permisses" name="third">

        <el-form label-width="120px" label-position="top">
          <el-form-item v-if="store.state.auth.id !== formData.id" label="Status do Usurio">
            <el-switch
                v-model="formData.disabled"
                inactive-text="Desabilitado"
                active-text="Habilitado"
                :active-value="false"
                :inactive-value="true"
                :disabled="isSupAdmin"
            >
            </el-switch>
          </el-form-item>

          <el-form-item v-if="store.state.auth.id !== formData.id && store.state.auth.administrator" label="Administrador">
            <el-switch
                v-model="formData.administrator"
                inactive-text="No"
                active-text="Sim"
                :disabled="isSupAdmin"
            >
            </el-switch>
          </el-form-item>
          <div  v-if="store.state.auth.id !== formData.id && store.state.auth.administrator" style="display: flex;align-content: space-between;justify-content: space-between">
              <div >
          <el-form-item v-if="!formData.administrator" label="Somente Leitura">
            <el-switch
                v-model="formData.readonly"
                inactive-text="No"
                active-text="Sim"
                :disabled="isSupAdmin"
            >
            </el-switch>
          </el-form-item>
              </div>
            <div >


          <el-form-item  v-if="!formData.administrator && !formData.readonly" label="Editar dispositivos">
            <el-switch
                v-model="formData.deviceReadonly"
                inactive-text="No"
                active-text="Sim"

                :active-value="false"
                :inactive-value="true"
                :disabled="isSupAdmin"
            >
            </el-switch>
          </el-form-item>

            </div>
            <div >

          <el-form-item  v-if="!formData.administrator && !formData.readonly" label="Enviar comandos">
            <el-switch
                v-model="formData.limitCommands"
                inactive-text="No"
                active-text="Sim"

                :active-value="false"
                :inactive-value="true"
                :disabled="isSupAdmin"
            >
            </el-switch>
          </el-form-item>
            </div>
          </div>

          <el-form-item label="Data de Expirao" >
            <el-input v-model="formData.expirationTime" :disabled="!store.state.auth.administrator && !(store.state.auth.id !==formData.id)"></el-input>
          </el-form-item>
          <el-form-item v-if="store.state.auth.id !== formData.id && store.state.auth.administrator" label="Limite de Dispositivos" >
            <el-switch
                v-model="formData.deviceLimit"
                inactive-text="Definido"
                active-text="Ilimitado"

                :active-value="-1"
                :inactive-value="0"
                :disabled="isSupAdmin"
            >
            </el-switch>
            <el-input v-if="formData.deviceLimit!==-1" v-model="formData.deviceLimit"></el-input>
          </el-form-item>

          <el-form-item v-if="store.state.auth.id !== formData.id && store.state.auth.administrator" label="Limite de Usurios" >
            <el-switch
                v-model="formData.userLimit"
                inactive-text="Definido"
                active-text="Ilimitado"

                :active-value="-1"
                :inactive-value="0"
                :disabled="isSupAdmin"
            >
            </el-switch>
            <el-input v-if="formData.userLimit!==-1" v-model="formData.userLimit" :disabled="isSupAdmin"></el-input>
          </el-form-item>

          <el-form-item label="Token" >
            <el-input v-model="formData.token" :disabled="isSupAdmin"></el-input>
          </el-form-item>
        </el-form>

      </el-tab-pane>
      <el-tab-pane v-if="store.state.auth.administrator || store.state.auth.id !== formData.id" label="Atributos" name="fourth">

        <tab-attributes v-model="formData.attributes" :type="'user'"></tab-attributes>

      </el-tab-pane>
    </el-tabs>
  </el-dialog>
</template>


<script setup>
import {ref,defineExpose,computed} from 'vue';
import {useStore} from 'vuex'

const store = useStore();

import {ElDialog, ElForm, ElFormItem, ElInput, ElMessage} from 'element-plus'
import TabAttributes from "./tab-attributes";


const title = ref('');

const show = ref(false);
const tab = ref('first');


const defaultUserData = {
    "id": 0,
    "name": "",
    "email": "",
    "phone": "",
    "readonly": false,
    "administrator": false,
    "map": "",
    "latitude": "",
    "longitude": "",
    "zoom": "",
    "password": "",
    "twelveHourFormat": false,
    "coordinateFormat": "",
    "disabled": false,
    "expirationTime": "",
    "deviceLimit": 0,
    "userLimit": 0,
    "deviceReadonly": true,
    "limitCommands": false,
    "poiLayer": "",
    "token": "",
    "attributes": {}
  }


// eslint-disable-next-line no-undef
const formData = ref(defaultUserData);




const newUser = ()=>{
  tab.value = 'first';
  title.value = 'Cadastrar Usurio';
  // eslint-disable-next-line no-undef
    formData.value = JSON.parse(JSON.stringify(defaultUserData));
    show.value = true;
}

const editUser = (id)=>{

  if(id===0){
    ElMessage.error('Voc precisa selecionar um usurio para editar.');
    return false;
  }


  title.value = 'Editar Usurio';
  tab.value = 'first';
  // eslint-disable-next-line no-undef
  formData.value = JSON.parse(JSON.stringify(defaultUserData));
  let device = null;
  if(id) {
    device = store.getters["users/getUser"](id);
  }else{
    device = store.getters["users/getUser"](store.state.auth.id);
  }

  // eslint-disable-next-line no-undef
  for(let k of Object.keys(defaultUserData)){
    formData.value[k] = device[k];
  }

  show.value = true;
}

defineExpose({
  newUser,
  editUser
});


const doCancel = ()=>{
  show.value = false;
}


const isSupAdmin = computed(()=>{

  const user = store.getters["users/getUser"](formData.value.id);


  if(user) {
    return (user.id < store.state.auth.id && user.administrator);
  }else{
    return false;
  }
})


const doSave = ()=>{

  if(isSupAdmin.value){
    ElMessage.error('Voc no pode editar um admin superior a voc.');
    return false;
  }

  store.dispatch("users/save",formData.value).then(()=>{
      show.value = false;
  })
}


</script>

<style>

.el-select.el-select--large{
  width: 100%;
}

.el-dialog__header,.el-dialog__body,.el-dialog__footer{
  padding: 0px !important;
}

.el-dialog__footer{
  margin-top: 20px;
}

.el-tabs__nav-wrap{
  padding-left: 20px;
  padding-right: 20px;
}

.el-tabs__content{
  padding-left: 20px;
  padding-right: 20px;
}

.el-form--label-top .el-form-item__label{
  padding: 0px !important;
  margin-bottom: -5px !important;
}
.el-form-item{
  margin-bottom: 5px !Important;
}

</style>